body {
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  margin-top: 10%;
}

.timer {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 30px;
}

.datetime-picker {
  width: 260px;
  height: 70px;
  border-radius: 5px;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 30px;
  box-shadow: 2px 2px 1px gray;
}

.datetime-picker:hover {
  background-color: #cdcdcd;
}

button {
  height: 70px;
  cursor: pointer;
  background-color: #fff;
  border-radius: 5px;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 30px;
  box-shadow: 2px 2px 1px gray;
}

button:hover {
  background-color: #cdcdcd;
}

@keyframes explode {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: .5;
    transform: scale(1.5);
  }

  100% {
    opacity: 0;
    transform: scale(2);
  }
}

#explosion {
  width: 400px;
  height: 400px;
  z-index: 10;
  background-image: url("powerpoint-animation-explosion-clip-art-png-favpng-eRpra7f2Qy5jdk0g0PT7u7XrX.12eb1415.png");
  background-repeat: no-repeat;
  background-size: contain;
  animation: explode 1s ease-in-out forwards;
  display: none;
  position: absolute;
}

p {
  position: absolute;
  top: 0;
  left: 0;
}

